  0%,
  90% {
    opacity: 1;
  }
  45% {
    opacity: 0;
  }
}
@keyframes flashing {
  0%,
  90% {
    opacity: 1;
  }
  45% {
    opacity: 0;
  }
}
@-webkit-keyframes fade-left {
  0% {
    -webkit-transform: translateX(0);
