/*
Typography
=========================== */
html,
body {
  padding: 0;
  margin: 0;
}

body {
  color: #6a6a6a;
  background: #fff;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 20px;
}

a {
  outline: 0;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  -khtml-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
a:focus {
  outline: 0;
}
a:hover {
  text-decoration: underline;
}
a.link {
  color: #6a6a6a;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #3a3a3a;
  margin: 0 0 15px 0;
  font-size: 48px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  line-height: 52px;
  text-transform: uppercase;
}

h2 {
  font-size: 36px;
  line-height: 48px;
}

h3 {
  font-size: 30px;
  line-height: 36px;
}

h4 {
  font-size: 24px;
  line-height: 30px;
}

h5 {
  font-size: 18px;
  line-height: 24px;
}

h6 {
  font-size: 14px;
  line-height: 18px;
}

p {
  margin-top: 0;
  margin-bottom: 20px;
}

.core-content {
  -webkit-transition: all 0.5s ease-in-out;
  -khtml-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.core-content .brand {
  outline: 0;
}
.core-content .brand img {
  border: 0;
  outline: 0;
}
.core-content .side-menu {
  position: fixed;
  overflow-y: auto;
  top: 0;
  right: -280px;
  width: 280px;
  height: 100%;
  display: block;
  border-left: solid 1px #efefef;
  -webkit-transition: all 0.5s ease-in-out;
  -khtml-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  z-index: 9;
}
.core-content.open-side-menu {
  margin-left: -280px;
  margin-right: 280px;
}
.core-content.open-side-menu .side-menu {
  right: 0;
}
.core-content.open-side-menu nav.nav-core-sticky.on-scroll {
  margin-left: -280px;
  margin-right: 280px;
}
.core-content .wrap-search-top {
  display: none;
  border-bottom: solid 1px #efefef;
}
.core-content .wrap-search-top input {
  border: 0;
  padding: 15px;
  display: block;
  width: 100%;
  outline: none;
  font-size: 14px;
  color: #6a6a6a;
}
.core-content .wrap-search-top ::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-size: 14px;
  font-style: italic;
}
.core-content .wrap-search-top ::-moz-placeholder {
  /* Firefox 19+ */
  font-size: 14px;
  font-style: italic;
}
.core-content .wrap-search-top :-ms-input-placeholder {
  /* IE 10+ */
  font-size: 14px;
  font-style: italic;
}
.core-content .wrap-search-top :-moz-placeholder {
  /* Firefox 18- */
  font-size: 14px;
  font-style: italic;
}
.core-content .wrap-search-fullscreen {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  display: none;
}
.core-content .wrap-search-fullscreen .nav-container {
  position: relative;
  padding-top: 50px;
  padding-left: 15px;
  padding-right: 15px;
}
.core-content .wrap-search-fullscreen.open {
  display: block;
}
.core-content .wrap-search-fullscreen .close-search {
  position: absolute;
  right: 0;
  top: 15px;
  padding: 15px;
  border: 0;
  background: transparent;
  outline: 0;
  cursor: pointer;
  color: #6a6a6a;
  z-index: 3;
  font-size: 24px;
}
.core-content .wrap-search-fullscreen input {
  display: block;
  width: 100%;
  border: 0;
  border-bottom: solid 1px #efefef;
  padding: 15px;
  outline: 0;
  font-size: 24px;
}

/*
Core Navigation
=========================== */
.core-nav {
  display: block;
  position: relative;
}
.core-nav.open-dropdown {
  z-index: 99999999;
}
.core-nav .nav-header {
  display: block;
  position: absolute;
  z-index: 2;
}
.core-nav .nav-header.left {
  float: left;
  position: relative;
  display: inline-block;
}
.core-nav .nav-header.center .brand {
  display: block;
  margin: auto;
}
.core-nav .nav-header .content-header {
  float: right;
}
.core-nav .core-nav-toggle {
  display: none;
  border: 0;
  padding: 0;
  background: transparent;
  outline: none;
  cursor: pointer;
}
.core-nav .wrap-core-nav-list {
  text-align: left;
  position: relative;
  z-index: 1;
}
.core-nav .wrap-core-nav-list.right {
  text-align: right;
}
.core-nav .wrap-core-nav-list.center {
  text-align: center;
}
.core-nav .wrap-core-nav-list .core-nav-list {
  display: inline-block;
  margin: 0;
  padding: 0;
  list-style: none;
}
.core-nav .wrap-core-nav-list .core-nav-list li {
  display: block;
  float: left;
}
.core-nav.nav-core-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.core-nav.nav-core-sticky {
  position: relative;
}
.core-nav.nav-core-sticky.on-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

@media (min-width: 992px) {
  .core-nav .nav-header.bottom {
    position: relative;
    float: none;
    display: block;
    width: 100%;
  }
  .core-nav .wrap-core-nav-list.bottom {
    position: relative;
    float: none;
    display: block;
    width: 100%;
  }
}

@media (max-width: 992px) {
  .core-nav .nav-header {
    position: relative;
    width: 100%;
    display: block;
  }
  .core-nav .nav-header.left {
    float: none;
    display: block;
    position: relative;
  }
  .core-nav .nav-header::after {
    content: "";
    clear: both;
    display: table;
  }
  .core-nav .nav-header .content-header {
    float: none;
  }
  .core-nav .core-nav-toggle {
    display: inline-block;
    float: left;
  }
  .core-nav .wrap-core-nav-list {
    width: 100%;
    max-height: 300px;
    overflow: auto;
    left: 0;
    margin-top: -100vh;
    position: absolute;
    border-bottom: solid 1px #dfdfdf;
    border-top: solid 1px #dfdfdf;
    filter: alpha(opacity=0);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    -webkit-opacity: 0;
    -khtml-opacity: 0;
    -moz-opacity: 0;
    -ms-opacity: 0;
    -o-opacity: 0;
    opacity: 0;
    -webkit-transition: all 0.5s ease-in-out;
    -khtml-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
  }
  .core-nav .wrap-core-nav-list .core-nav-list {
    margin: 0;
    display: block;
    background: #ffffff;
  }
  .core-nav .wrap-core-nav-list .core-nav-list li {
    float: none;
    display: block;
    width: 100%;
  }
  .core-nav .wrap-core-nav-list .core-nav-list li a {
    display: block;
    width: 100%;
    text-align: left;
  }
  .core-nav .nav-header.bottom {
    border: 0;
  }
  .core-nav.open-responsive .wrap-core-nav-list {
    margin-top: 0;
    filter: alpha(opacity=100);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    -webkit-opacity: 1;
    -khtml-opacity: 1;
    -moz-opacity: 1;
    -ms-opacity: 1;
    -o-opacity: 1;
    opacity: 1;
  }
  .core-responsive-slide {
    -webkit-transition: all 0.5s ease-in-out;
    -khtml-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    filter: alpha(opacity=100);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    -webkit-opacity: 1;
    -khtml-opacity: 1;
    -moz-opacity: 1;
    -ms-opacity: 1;
    -o-opacity: 1;
    opacity: 1;
  }
  .core-responsive-slide .core-nav .wrap-core-nav-list {
    top: 0;
    width: 270px;
    max-height: 100vh;
    height: 100vh;
    margin-top: 0;
    margin-left: -270px;
    border: 0;
    border-right: solid 1px #dfdfdf;
    position: fixed;
    -webkit-transition: all 0.5s ease-in-out;
    -khtml-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
  }
  .core-responsive-slide.open {
    margin-left: 270px;
    margin-right: -270px;
  }
  .core-responsive-slide.open .core-nav .wrap-core-nav-list {
    top: 0;
    margin-top: 0;
    margin-left: 0;
    filter: alpha(opacity=100);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    -webkit-opacity: 1;
    -khtml-opacity: 1;
    -moz-opacity: 1;
    -ms-opacity: 1;
    -o-opacity: 1;
    opacity: 1;
  }
}

/*
Fullscreen
=========================== */
@media (min-width: 920px) {
  .core-nav.fullscreen .nav-header {
    width: 100%;
    text-align: left;
    padding: 0;
    position: relative;
  }
  .core-nav.fullscreen .nav-header .brand {
    top: 0;
