.style-switcher {
  position: fixed;
  top: 35%;
  transition: 0.4s ease-in-out;
  background-color: #fff;
  left: -220px;
  width: 220px;
  padding: 0;
  z-index: 1010;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
