  position: absolute;
  top: 0;
  width: 30px;
  height: 30px;
  opacity: 0;
  line-height: 30px;
  background: rgba(255, 255, 255, 0.2);
  font-size: 10px;
  color: #ffffff;
  cursor: pointer;
  border-radius: 50%;
