  justify-content: center;
  align-items: center;
  background: #ffffff;
  border-radius: 6px;
  padding: 1.2em 2em;
  box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -webkit-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
}
.icon-mi-left i {
  font-size: 55px;
  color: #333;
  text-align: left;
  position: relative;
  top: 5px;
  left: 0;
  line-height: 55px;
}
.icon-mi-left .icon-mi-left-content {
  padding: 10px 0 0 15px;
}
.icon-mi-left h4 {
  text-align: left;
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: 600;
}
.icon-mi-left p {
  line-height: 1.7;
}

/*--------- FAQ Design -----------*/
.faq-search form {
  position: relative;
  max-width: 610px;
  margin: 1.5rem auto;
}
.faq-search form input {
  padding-left: 30px !important;
  height: 60px;
  border: none;
  border-radius: 4px;
  padding-right: 50px !important;
}
.faq-search form button[type="submit"] {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 50px;
  line-height: 63px;
  right: 0;
  color: #0050b4;
  font-size: 18px;
  border: 0;
  cursor: pointer;
  background-color: transparent;
}
.ac-para {
  line-height: 1.8;
}
.contact-box {
  border-radius: 10px;
  overflow: hidden;
  padding: 2.5em 2em;
  margin-bottom: 30px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -webkit-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -moz-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
}
.contact-box i {
  font-size: 3em;
  display: table;
  margin: 0 auto 1rem auto;
}
.contact-box h4 {
  font-size: 18px;
  margin-bottom: 3px;
}
.contact-box p {
  margin-bottom: 0.3rem;
}
.contact-box .live-chat {
  background: rgba(25, 189, 88, 0.1);
  color: #19bd58;
  border: none;
  border-radius: 2px;
  padding: 4px 12px;
  cursor: pointer;
  outline: none;
  display: table;
  margin: 5px auto 0 auto;
}

/*----------- Checkout Design -----------*/
.pay-wrap {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -webkit-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -moz-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
}
.pay-wrap-header {
  width: 100%;
  padding: 12px 25px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #e7eaf1;
}
.pay-wrap-header h4 {
  flex: 1 1;
  margin: 0;
  font-size: 20px;
  font-weight: 500;
}
.pw-right h3 {
  color: red;
  margin: 0;
}
.pw-right h3 sub {
  font-size: 15px;
  font-weight: 500;
  color: #767a8a;
}
.pw-first-content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pw-first-content h4 {
  flex: 1 1;
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 600;
}
.pw-first-content {
  padding: 10px 25px;
}
.pay-wrap button,
.pay-wrap button:hover,
.pay-wrap button:focus {
  background: rgba(25, 189, 88, 0.1);
  color: #19bd58;
  border: none;
  border-radius: 2px;
  padding: 4px 12px;
  cursor: pointer;
  outline: none;
}
#change-plan {
  padding: 10px 25px;
  position: relative;
}
.pw-content-detail {
  padding: 10px 25px;
  width: 100%;
  position: relative;
}
.pw-content-detail ul {
  position: relative;
  padding: 0;
  margin: 0;
}
.pw-content-detail ul li {
  position: relative;
  display: block;
  margin-bottom: 10px;
  list-style: none;
  padding-left: 30px;
}
.pw-btn-wrap {
  padding: 10px 25px 25px;
  width: 100%;
  position: relative;
}
.btn.btn-payment {
  background: #ffffff;
  color: #19bd58;
  text-align: center;
  width: 100%;
  border: 2px solid #19bd58;
  font-size: 17px;
  border-radius: 4px;
}
.pw-content-detail ul li:before {
  content: "\e64c";
  font-family: "themify";
  font-size: 10px;
  width: 20px;
  height: 20px;
  background: rgba(25, 189, 88, 0.1);
  display: flex;
  position: absolute;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  color: #19bd58;
  top: 0;
  left: 0;
}
.product-wrap {
  border-radius: 10px;
  overflow: hidden;
  padding: 2em 1rem;
  margin-bottom: 30px;
  box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -webkit-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
  -moz-box-shadow: 0 0 20px 0 rgba(62, 28, 131, 0.1);
}
.product-wrap ul {
  padding: 0;
  margin: 0;
}

.product-wrap ul li {
  display: flex;
  padding: 14px 0;
  border-bottom: 1px dashed #dadde8;
  flex-wrap: wrap;
}
.product-wrap ul li strong {
  flex: 1 1;
}
/*====================================
 Bottom To top Scroll
=====================================*/
#back2Top {
  width: 40px;
  line-height: 40px;
  overflow: hidden;
  z-index: 999;
  display: none;
  cursor: pointer;
  position: fixed;
  bottom: 10px;
  right: 20px;
  text-align: center;
  font-size: 15px;
  border-radius: 4px;
  text-decoration: none;
  background: #333c56;
  color: #ffffff;
}
#back2Top:hover {
  background-color: #ffffff;
  color: #333c56;
}

.style-switcher {
  position: fixed;
  top: 35%;
  -webkit-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
  background-color: #fff;
  left: -220px;
  width: 220px;
  padding: 0;
  z-index: 1010;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
}
.style-switcher .css-trigger {
  position: absolute;
  top: 37%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: -50px;
  width: 50px;
  background-color: #2d4767;
  color: #fff;
  border-radius: 0px 3px 3px 0px;
  text-align: center;
  line-height: 55px;
  height: 50px;
  font-size: 24px;
  cursor: pointer;
}
.style-switcher .css-trigger a {
  color: #ffffff;
}
.style-switcher.active {
  left: 0;
}
ul#themecolors {
  margin: 0;
  padding: 15px 0;
  text-align: center;
  background: #334e6f;
}
ul#themecolors li {
  list-style: none;
  padding: 5px;
